import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { LoggerService } from './../../../commonSRC/psServices/logger/logger.service';
import { PsTemplateListPage } from './../../../commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from './../../psServices/models/ps-common-bussiness-constant';
import { IOptionsPsSignatoryCardExposed } from './ps-signatory-card.component.interface';

@Component({
  selector: 'ps-signatory-card',
  templateUrl: './ps-signatory-card.component.html',
  styleUrls: ['./ps-signatory-card.component.scss'],
})
export class PsSignatoryCardComponent extends PsTemplateListPage implements OnInit {
  @Input() options: IOptionsPsSignatoryCardExposed;
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  public signatoryLabelOptions: IOptionsPsLabel = {
    labelKey: 'signatory_list_not_available'
  };

  constructor(public logger: LoggerService, private cdRef?: ChangeDetectorRef) { super(); }

  ngOnInit() {
    super.init();
    this.populateHeaderValueMap(this.options.signatoryType);
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
    if (this.options && this.options.noSignatoriesLabel) {
      this.signatoryLabelOptions.labelKey = this.options.noSignatoriesLabel;
    }
  }

  getContainerLookUpOptions(val) {
    return this.prepareLookUp(val);
  }

  private prepareLookUp(signatoryCard: any): any {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.showInitialCardValues = 4;
    return lookupDetails;
  }


  private populateHeaderValueMap(signatoryType) {
    let cifNameMap: IMapKeyValue = {};
    let cifNumberMap: IMapKeyValue = {};
    let idNumberMap: IMapKeyValue = {};
    let idTypeMap: IMapKeyValue = {};
    let nationalityMap: IMapKeyValue = {};
    let addressMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};

    cifNameMap = {
      key: 'cifName',
      value: '',
      formGroupMap: {
        fcName: 'cifName',
      }
    };
    cifNumberMap = {
      key: 'cifNumber',
      value: '',
      formGroupMap: {
        fcName: 'cifNumber',
      }
    };
    idNumberMap = {
      key: 'idNumber',
      value: '',
      formGroupMap: {
        fcName: 'idNumber',
      }
    };
    idTypeMap = {
      key: 'idType',
      value: '',
      formGroupMap: {
        fcName: 'idType',
      }
    };
    nationalityMap = {
      key: 'nationality',
      value: ''
    };
    addressMap = {
      key: 'address',
      value: ''
    };

    headerTitleMap = {
      key: 'cifName',
      value: 'cifName',
      isEdit: false,
      formGroupMap: {
        fcName: 'cifName',
      }
    };
    headerSubtitleMap = {
      key: 'cifNumber',
      value: 'cifNumber',
      isEdit: true,
      formGroupMap: {
        fcName: 'cifNumber',
      }
    };

    // key here will be used as label in look up details component
    // this.labelValuesMap.set('cif_name_key', cifNameMap);
    // this.labelValuesMap.set('cif_number_key', cifNumberMap);
    if (signatoryType === CommonBussinessConstant.SIGNATORY_SEGMENT_NAME_AUTHORIZED) {
      this.labelValuesMap.set('id_number_key', idNumberMap);
      this.labelValuesMap.set('id_type_key', idTypeMap);
      this.labelValuesMap.set('nationality_key', nationalityMap);
      this.labelValuesMap.set('address_key', addressMap);
    }

    // will be used as header to template card in look up details component
    this.headerMap.set('cifName', headerTitleMap);
    this.headerMap.set('cifNumber', headerSubtitleMap);
  }

  // eslint-disable-next-line @angular-eslint/use-lifecycle-interface
  ngAfterViewChecked() {
    this.cdRef.markForCheck();
  }
}
